<template>
  <v-container
    fill-height
    fluid
    grid-list-xl
  >
    <v-row justify="center">
      <v-col cols="12">

        <v-card color="green" class="white--text pa-3">
          <v-row>
            <v-col cols="10">
              <h3 class="title font-weight-light"> Servios </h3>
              <h4 class="category font-weight-thin mb-0 "> Visualize servios cadastrados</h4>
            </v-col>

            <v-col cols="2">
              <product-new/>
            </v-col>
          </v-row>
        </v-card>

        <v-data-table
          :headers="headers"
          :items="products"
        >
          <template v-slot:item.title="{ item }">
            {{item.title}}
          </template>

          <template v-slot:item.price="{ item }">
            R$ {{item.price}}
          </template>

          <template v-slot:item.description="{ item }">
            {{item.description}}
          </template>

          <template v-slot:item.establishment="{ item }">
            {{item.name}}
          </template>

          <template v-slot:item.action="{ item }">
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-icon
                  color="error"
                  class="cursor-type"
                  large v-on="on"
                  @click="deleteProduct( item.id )"
                >
                  mdi-delete-circle
                </v-icon>
              </template>
              <span> Excluir Servio </span>
            </v-tooltip>

            <product-edit :product_id="item.id"/>

          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import { securedAxiosInstance } from '../plugins/axios'

  export default {
    components: {
      ProductNew: () => import('@/views/ProductsNew'),
      ProductEdit: () => import('@/views/ProductsEdit'),
    },

    mounted() {
      this.establishment_id = this.$store.state.selectedEstablishment.id
      this.getProducts( this.establishment_id )
    },

    data: () => ({
      establishment_id: null,

      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      products: [],
      error: false,

      headers: [
        {
          sortable: false,
          text: 'Nome',
          value: 'title'
        },
        {
          sortable: false,
          text: 'Preo (R$)',
          value: 'price'
        },
        {
          sortable: false,
          text: 'Descrio',
          value: 'description'
        },
        {
          sortable: false,
          text: 'Estabelecimento',
          value: 'establishment.name'
        },
        {
          text: 'Ao',
          value: 'action'
        }
      ],
    }),

    methods: {
      async getProducts( id ) {
        let response = null
        await securedAxiosInstance.get(`/api/v1/establishments/${id}/products`,
          {
            params: { access_token: localStorage.getItem('access_token') }
          }
        )
        .then((result) => {
          response = result
        }).catch((err) => {
          response = err
        });

        if ( response.status == 200 ) {
          this.products = response.data

        } else {
          this.error = true
        }
      },

      async deleteProduct( id ) {
        let response = null

        await securedAxiosInstance.delete(`/api/v1/products/${id}`,
          {
            params: { access_token: localStorage.getItem('access_token') }
          }
        )
        .then((result) => {
          response = result

        }).catch((err) => {
          response = err
        });

        if ( response.status == 200 ) {
          this.products = []
          this.getProducts()
        }
      },

      editProduct( id ) {

      }
    },


  }
</script>


<style scoped>

  .cursor-type {
    cursor: pointer;
  }
